.skillsBlock {
    background-color: #fff3b5;
    min-height: 50vh;

}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;

}

.title {
    background-color: slategray;
}


.skills {
    border: 1px solid red;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
}
